<template>
  <div class="app-map" id="map"></div>
</template>

<script>
import mapboxgl from "!mapbox-gl";

export default {
  name: "AppMap",
  props: {
    msg: String,
  },
  data() {
    return {
      accessToken: process.env.VUE_APP_MAP_ACCESS_TOKEN,
      map: null,
      myPoints: {
        type: "geojson",
        data: {
          type: "FeatureCollection",
          features: [],
        },
      },
      layer: {
        id: "points",
        type: "circle",
        source: "points",
        layout: {},
        paint: {
          "circle-color": ["get", "color"],
          "circle-radius": 10,
        },
      },
      featID: null
    };
  },
  watch: {
    "$store.state.search": function () {
      this.myPoints.data.features = this.arrayStations;
      this.map.removeLayer("points");
      this.map.removeSource("points");
      this.map.addSource("points", this.myPoints);
      this.map.addLayer(this.layer);
    },
  },
  computed: {
    arrayStations() {
      return this.$store.getters["getStationsWithBranchColor"];
    },
  },
  methods: {
    openPopup() {
      this.$store.dispatch("popupUpdate", true);
    },
    initMap() {
      mapboxgl.accessToken = this.accessToken;
      this.map = new mapboxgl.Map({
        container: "map",
        style: "mapbox://styles/mapbox/dark-v10",
        center: [37.617698, 55.755864],
        zoom: 11,
        projection: "globe",
      });
    },
  },
  async mounted() {
    await this.$store.dispatch("getBranches");
    this.myPoints.data.features = this.arrayStations;
    this.initMap();

    this.map.on("load", async () => {
      this.map.setFog({});

      this.map.addSource("points", this.myPoints);
      this.map.addLayer(this.layer);

      this.map.on("click", "points", (e) => {
        console.log(e.lngLat);
        this.openPopup();
      });

      this.map.on("mousemove", "points", () => {
        this.map.getCanvas().style.cursor = "pointer";

        if (this.featID) {
          this.map.removeFeatureState({
            source: "points",
            id: this.featID,
          });
        }

        this.map.setFeatureState(
          {
            source: "points",
            id: this.featID,
          },
          {
            hover: true,
          }
        );
      });
    });

    this.map.on('mouseleave', 'points', () => {
      if (this.featID) {
        this.map.setFeatureState(
          {
            source: 'points',
            id: this.featID
          },
          {
            hover: false
          }
        );
      }

      this.featID = null;


      this.map.getCanvas().style.cursor = '';
    });
  },
  unmounted() {
    this.map.remove();
    this.map = null;
  },
};
</script>

<style>
.app-map {
  height: 100vh;
  flex: 1;
}
</style>



